body {
    font: 14px "Century Gothic", Futura, sans-serif;
    margin: 20px;
  }
  
  ol, ul {
    padding-left: 30px;
  }
  
  .board-row:after {
    clear: both;
    content: "";
    display: table;
  }
  
  .status {
    margin-bottom: 10px;
  }

  .blob {
    background: #ffffff;
    border:4px solid black;
    border-radius: 50%;
    height: 40px;
    width: 40px;
    position: absolute;
    transition: all 1s ease-out 0s;
    top:256px;
    left:256px;
  }
  
  .blob.blue {
    background: #456BD9;
  }

  .blob.blue.highlight {
    background: #16379b;
    border:4px solid rgb(151, 134, 224);
    transition: all 0.5s ease-out 0s;
  }

  .blob.red {
    background: #d94545;
  }

  .blob.red.highlight {
    background: #971919;
    border:4px solid rgb(255, 167, 167);
    transition: all 0.5s ease-out 0s;
  }

  .blob.display.enabled{
    height: 56px;
    width: 56px;
    position: relative;
    top:0;
    left:0;
    opacity: 1;
  }

  .blob.display.disabled{
    height: 56px;
    width: 56px;
    position: relative;
    top:0;
    left:0;
    opacity: 0.25;
  }

  .square {
    background: rgb(230, 201, 255);
    border: 1px solid rgb(255, 255, 255);
    float: left;
    font-size: 24px;
    font-weight: bold;
    line-height: 64px;
    height: 64px;
    text-align: center;
    width: 64px;
  }
  
  .square.neighbor {
    background: rgb(155, 40, 255);
  }

  .square.jump {
    background: rgb(203, 144, 255);
  }
  
  .kbd-navigation .square:focus {
    background: #ddd;
  }
  
  .game {
    display: flex;
    flex-direction: column; 
    position: absolute; 
    left: 50%;
    transform: translate(-50%, 0);
    margin-top: 50px;
    border:2px solid rgb(115, 2, 214);
  }
  
  .game-info {
    display: flex;
    flex-direction:row;
    background: rgb(239, 220, 255);
    border-top:2px solid rgb(115, 2, 214);
    padding-top: 8px;
    padding-bottom: 8px;
  }

  .red-info {
    display: flex;
    flex-direction:row; 
    margin-right: auto;
    margin-left: auto;
  }

  .blue-info {
    display: flex;
    flex-direction:row; 
    margin-right: auto;
    margin-left: auto;
  }

  @import url("https://fonts.googleapis.com/css?family=Modak");
  @import url("https://fonts.googleapis.com/css?family=Palanquin");
  @import url('https://fonts.googleapis.com/css2?family=Palanquin+Dark&display=swap');
  .red-score-text {
    margin: auto;
    margin-right: 32px;
    font-size: 36px;
    font-family: 'Palanquin Dark';
    color: #d94545;
  }

  .blue-score-text {
    margin: auto;
    margin-left: 32px;
    font-size: 36px;
    font-family: 'Palanquin Dark';
    color: #456BD9;
  }
  
  .game-board{
  }
  
  .menu{
    background: rgb(239, 220, 255);
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .restart-text{
    font-size: 24px;
    font-family: 'Palanquin Dark';
    color: #000000;
  }

  .restart-button{
    background: rgb(216, 168, 255);
    margin-left: 8px;
    margin-right: 8px;
    border-radius: 8px;
  }

  .face{
    height: 16wh;
    width: 16wh;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .eye{
    width: 16px;
    height: 16px;
    background: white;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 6px;
    clip-path: circle(50% at center);
    position: relative;
  }

  .iris{
    width: 50%;
    height: 50%;
    background: green;
    border-radius: 50%;
    animation: movement 6s ease-in-out infinite;
  }

  .upper-pupil,
  .lower-pupil{
    width: 100%;
    height: 8px;
    background: black;
    position: absolute;
    animation: blink 6s ease-in-out infinite;
    z-index: 1;
  }

  .upper-pupil{
    top:0;
  }

  .lower-pupil{
    bottom:0;
  }

  @keyframes movement {
    0%, 10% {
      transform: translateX(0);
    }
    20% {
      transform: translateX(-80%);
    }
    30%, 80% {
      transform: translateX(0);
    }
    90% {
      transform: translateX(80%);
    }
    100% {
      transform: translateX(0);
    }
  }

  @keyframes blink {
    0%, 30% {
      height: 45%;
    }
    32% {
      height: 40%;
    }
    34% {
      height: 20%;
    }
    36% {
      height: 20%;
    }
    38%, 50% {
      height: 20%;
    }
    60%, 70% {
      height: 40%;
    }
    80%, 100% {
      height: 45%;
    }
  }